import React from "react";
import { Box } from "@mui/material";

export default function PopUp() {
    return (
        <Box
        sx={{
            position: 'absolute',
            top: '0',
            bottom: '0',
            left: '0',
            right: '0',
            zIndex: '10',
            display: 'flex',
            alignItems: "center",
            justifyContent: 'center',
        }}
        >

        </Box>
    );
}